import React, { useState, useEffect } from "react";
import Section1 from "./sections/section1";
import Section2 from "./sections/section2";
import Section3 from "./sections/section3";
import Section4 from "./sections/section4";
import Section5 from "./sections/section5";
import Section6 from "./sections/section6";
import Section7 from "./sections/section7";
import ConfusedCard from "../explore/confusedCard";
import { Box, Button, Typography } from "@mui/material";

const steps = [
  { text: "Welcome! Let's set up your account.", buttonText: "Next" },
  { text: "Tell us about yourself to personalize your experience.", buttonText: "Next" },
  { text: "You're all set! Click below to continue.", buttonText: "Finish" },
];

const OnboardingPopup = ({ onComplete }: { onComplete: () => void }) => {
  const [step, setStep] = useState(0);

  return (
    <Box
      sx={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        backgroundColor: "rgba(0,0,0,0.6)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      <Box
        sx={{
          background: "white",
          padding: "20px",
          borderRadius: "10px",
          textAlign: "center",
        }}
      >
        <Typography variant="h6">{steps[step].text}</Typography>
        <Button
          variant="contained"
          onClick={() =>
            step < steps.length - 1 ? setStep(step + 1) : onComplete()
          }
          sx={{ mt: 2 }}
        >
          {steps[step].buttonText}
        </Button>
      </Box>
    </Box>
  );
};

const Home = () => {
  const [showOnboarding, setShowOnboarding] = useState<boolean>(
    !localStorage.getItem("onboardingCompleted")
  );

  useEffect(() => {
    if (!showOnboarding) {
      localStorage.setItem("onboardingCompleted", "true");
    }
  }, [showOnboarding]);

  return (
    <div style={{ margin: "5rem 0", width: "100%", overflow: "hidden" }}>
      {showOnboarding ? (
        <OnboardingPopup onComplete={() => setShowOnboarding(false)} />
      ) : (
        <>
          <Section1 />
          <Section2 />
          <Box
            sx={{
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <Box
              sx={{
                maxWidth: "1440px",
                width: { lg: "100%", xs: "90%" },
                position: "relative",
              }}
            >
              <Box
                sx={{
                  padding: { xs: "0rem", lg: "0rem 10rem" },
                  my: 2,
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <ConfusedCard />
              </Box>
            </Box>
          </Box>
          <Section3 />
          <Section4 />
          <Section7 />
          <Section5 />
          <Section6 />
        </>
      )}
    </div>
  );
};

export default Home;
